"use client";

import { motion } from "framer-motion";
import Link from "next/link";
import { ArrowLeft, Users, Bed, Layers, Home } from "lucide-react";

const rooms = [
    { id: "101", type: "Delux", bed: "Double", floor: "1", capacity: "2 Adults, 1 Children", status: "Occupied" },
    { id: "201", type: "Premium", bed: "King", floor: "2", capacity: "2 Adults, 1 Children", status: "Occupied" },
    { id: "202", type: "Premium", bed: "King", floor: "1", capacity: "2 Adults, 1 Children", status: "Occupied" },
    { id: "106", type: "Delux", bed: "Double", floor: "1", capacity: "2 Adults, 1 Children", status: "Occupied" },
    { id: "108", type: "Standard", bed: "Single", floor: "1", capacity: "1 Adult", status: "Vacant" },
    { id: "305", type: "Suite", bed: "King", floor: "3", capacity: "4 Adults", status: "Cleaning" },
];

export default function RoomRecordsPage() {
    return (
        <div className="space-y-6">
            <div className="flex items-center justify-between">
                <div>
                    <h1 className="text-2xl font-bold text-slate-800">Room Records</h1>
                    <p className="text-slate-500 text-sm">Manage and view status of all rooms</p>
                </div>
                <Link
                    href="/dashboard"
                    className="bg-blue-600 hover:bg-blue-700 text-white px-4 py-2 rounded-lg text-sm font-medium flex items-center gap-2 transition-colors shadow-lg shadow-blue-500/30"
                >
                    <ArrowLeft className="w-4 h-4" />
                    Back to Dashboard
                </Link>
            </div>

            <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 xl:grid-cols-4 gap-6">
                {rooms.map((room, index) => (
                    <RoomCard key={room.id} room={room} index={index} />
                ))}
            </div>
        </div>
    );
}

function RoomCard({ room, index }: { room: any; index: number }) {
    const statusColors = {
        Occupied: "bg-red-100 text-red-700",
        Vacant: "bg-green-100 text-green-700",
        Cleaning: "bg-blue-100 text-blue-700",
    };

    return (
        <div className="h-full">
            <Link href={`/dashboard/records/${room.id}`}>
                <div className="bg-white rounded-2xl p-6 shadow-sm border border-slate-100 hover:shadow-xl hover:border-blue-200 transition-all duration-300 group cursor-pointer relative overflow-hidden">

                    {/* Header */}
                    <div className="flex justify-between items-start mb-4">
                        <h3 className="text-xl font-bold text-slate-800 group-hover:text-blue-600 transition-colors">
                            Room {room.id}
                        </h3>
                        <span className={`px-2.5 py-1 rounded-full text-xs font-bold uppercase tracking-wide ${statusColors[room.status as keyof typeof statusColors]}`}>
                            {room.status}
                        </span>
                    </div>

                    {/* Details */}
                    <div className="space-y-3">
                        <div className="flex justify-between text-sm">
                            <span className="text-slate-500 flex items-center gap-2">
                                <Home className="w-4 h-4" /> Type
                            </span>
                            <span className="font-semibold text-slate-700">{room.type}</span>
                        </div>
                        <div className="flex justify-between text-sm">
                            <span className="text-slate-500 flex items-center gap-2">
                                <Bed className="w-4 h-4" /> Bed
                            </span>
                            <span className="font-semibold text-slate-700">{room.bed}</span>
                        </div>
                        <div className="flex justify-between text-sm">
                            <span className="text-slate-500 flex items-center gap-2">
                                <Layers className="w-4 h-4" /> Floor
                            </span>
                            <span className="font-semibold text-slate-700">{room.floor}</span>
                        </div>
                        <div className="flex justify-between text-sm border-t border-slate-50 pt-3 mt-2">
                            <span className="text-slate-500 flex items-center gap-2">
                                <Users className="w-4 h-4" /> Capacity
                            </span>
                            <span className="font-semibold text-slate-700 text-right">{room.capacity}</span>
                        </div>
                    </div>

                    {/* Hover highlight */}
                    <div className="absolute inset-0 border-2 border-blue-500 rounded-2xl opacity-0 group-hover:opacity-100 transition-opacity pointer-events-none" />
                </div>
            </Link>
        </div>
    );
}
